<template>
  <form
    @click="close"
    class="city-modal"
  >
    <h2 class="city-modal__heading">
      Ваш город доставки
    </h2>
    <field
      ref="locationCountries"
      :list="countries"
      placeholder="Введите страну"
    />
    <field
      ref="locationCities"
      :list="cities"
      placeholder="Введите населённый пункт"
    />
    <button
      @click.prevent="$emit('close-modal')"
      class="city-modal__submit"
    >
      Да, верно
    </button>
    <i
      @click.prevent="$emit('close-modal')"
      class="icon-close"
    />
  </form>
</template>

<script>
import Field from './Field.vue'
export default {
  name: 'LocationForm',
  components: {
    Field
  },
  data () {
    return {
      countries: [
        'Австралия',
        'Венгрия',
        'Германия',
        'Италия',
        'Китай',
        'Литва',
        'Польша',
        'Россия',
        'США',
        'Чехия'
      ],
      cities: [
        'Куйбышев',
        'Купино',
        'Курильск',
        'Москва',
        'Волгоград',
        'Екатеринбург',
        'Уфа',
        'Норильск',
        'Красноярск',
        'Пенза'
      ]
    }
  },
  methods: {
    close (e) {
      const target = e.target
      if (target.classList.contains('city-modal')) {
        this.$refs.locationCountries.open = false
        this.$refs.locationCities.open = false
      }
    }
  }
}
</script>

